import React from 'react';
import { Grid, Container, Paper, Card, CardContent, Button, Typography } from '@mui/material';

const TaskPage = () => (
  <Container>
    <Grid container spacing={2} justifyContent="center">
      <Grid item xs={12} md={6}>
        <Paper elevation={3} style={{ padding: '20px' }}>
          <Typography variant="h5" gutterBottom>Add New Task</Typography>
          {/* Task Input Component */}
        </Paper>
      </Grid>
      <Grid item xs={12} md={6}>
        <Paper elevation={3} style={{ padding: '20px' }}>
          <Typography variant="h5" gutterBottom>Task List</Typography>
          <div>
            {/* Map over tasks and display them in a Card */}
            <Card>
              <CardContent>
                <Typography>Task Name</Typography>
                <Button variant="contained" color="primary">Complete</Button>
              </CardContent>
            </Card>
          </div>
        </Paper>
      </Grid>
    </Grid>
  </Container>
);

export default TaskPage;
