import React, { useState } from 'react';
import { Container, Grid, Card, CardContent, TextField, Button, Typography } from '@mui/material';
import { useDispatch } from 'react-redux';
import { login } from '../features/auth/authSlice'; // Ensure this path is correct

import { useSpring, animated } from 'react-spring';

const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const dispatch = useDispatch();

  const handleLogin = () => {
    dispatch(login({ email, password }));
  };

  // Animation using react-spring
  const props = useSpring({ opacity: 1, from: { opacity: 0 } });

  return (
    <animated.div style={props}>
      <Container>
        <Grid container justifyContent="center" style={{ minHeight: '100vh', backgroundColor: '#f5f5f5' }}>
          <Grid item xs={12} sm={6} md={4}>
            <Card elevation={5}>
              <CardContent style={{ textAlign: 'center' }}>
                <Typography variant="h4" gutterBottom>Login</Typography>
                <TextField
                  label="Email"
                  variant="outlined"
                  fullWidth
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  style={{ marginBottom: '20px' }}
                />
                <TextField
                  label="Password"
                  variant="outlined"
                  type="password"
                  fullWidth
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  style={{ marginBottom: '20px' }}
                />
                <Button 
                  variant="contained" 
                  color="primary" 
                  fullWidth 
                  onClick={handleLogin}
                  style={{
                    padding: '10px',
                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                    marginBottom: '20px',
                  }}
                >
                  Log In
                </Button>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>
    </animated.div>
  );
};

export default LoginPage;
